import React, {Component} from 'react';
import Link from 'next/link';
import Router from 'next/router';
import { runInThisContext } from 'vm';

class Index extends Component{
    static getInitialProps(context){
        console.log(context);
        //await (usar async no lifeCycle) para aguardar a promise
        const promiseSample = new Promise((resolve, reject)=>{
            setTimeout(()=>{
                resolve({appName: "The App"})
            }, 1000);
        });
        return promiseSample;
        //return {appName: "The App"};
    }

    render(){
        return(
            <div>
                <h1>Main page of {this.props.appName}</h1>
                <p>Ir para <Link href="/auth"><a >Auth</a></Link></p>
                <button onClick={()=>Router.push('/auth')}>Go to Auth</button>
            </div>
        );
    }

};

export default Index;


